/** @jsx jsx */
import { jsx } from '@emotion/core';
import { graphql } from 'gatsby';
import startCase from 'lodash/startCase';
import { Post } from 'models/post';
import PostsLayout from 'components/Layout/Posts';

interface Props {
  data: {
    allMarkdownRemark: any;
    site: {
      siteMetadata: {
        title: string;
      };
    };
  };
  location: Location;
}

const CategoryTemplate = ({ data, location }: Props) => {
  const {
    allMarkdownRemark: { edges: posts, pageInfo },
  } = data;
  const [, categoryPath, categorySlug] = location.pathname.split('/');
  const path = `/${categoryPath}/${categorySlug}`;

  return (
    <PostsLayout
      title={startCase(categorySlug)}
      posts={posts.map((p: { node: Post }) => p.node)}
      pageInfo={{ ...pageInfo, defaultUrl: path }}
      location={location}
    />
  );
};

export default CategoryTemplate;

export const pageQuery = graphql`
  query CategoryQuery($skip: Int!, $limit: Int!, $category: String!) {
    allMarkdownRemark(
      skip: $skip
      limit: $limit
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { categories: { in: [$category] } } }
    ) {
      edges {
        node {
          ...PostListQuery
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
